import React, { useState } from "react";
import RLogin, { RLoginButton } from "@rsksmart/rlogin";
import WalletConnectProvider from "@walletconnect/web3-provider";
import { ethers } from "ethers";
import Web3 from "web3";
import ABI from "../abis/abi.json";
import ABI2 from "../abis/Token.json";
import "../assets/css/pannel.css";

const rLogin = new RLogin({
  cachedProvider: false,
  providerOptions: {
    walletconnect: {
      package: WalletConnectProvider,
      options: { rpc: { 60457: "https://fufi.finance/rpc" } },
    },
  },
  supportedChains: [60457],
});


const Tradeox = () => {
  const [provider, setProvider] = useState(null);
  const [sessions, setSessions] = useState([]);
  let [account, setAccount] = useState("");
  const [balance, setBalance] = useState();
  const [Fusdbalance, setFusdBalance] = useState();
  const [fusdtlpbalance, setFusdtlpbalance] = useState();
  const [fufilpbalance, setFufilpbalance] = useState();
  const [addamount, setaddamount] = useState();
  const [removeamount, setremoveamount] = useState();
  const [Fusdamt, setFusdamt] = useState();
  const [RFusdamt, setRTotalam] = useState();
  const [FufiswapFusdamt, setFufiswapFusdamt] = useState();
  const [FusdswapRFusdamt, setFusdswapRTotalam] = useState();

  const [txHash, setaddTxHash] = useState("");
  const [txHash1, setremoveTxHash] = useState("");
  const [Totalfusdtlpbalance, setTotalFusdtlpbalance] = useState();
  const [Totalfufilpbalance, setTotalFufilpbalance] = useState();
  const [disConnects, setDisConnects] = useState("disConnects");
  const [disablecls, setDisablecls] = useState("disablecls");

  let CONTRACT_ADDRESS = "0x0F3Bcc18558C1ADC3C128d18f233DB26C96af474";
  let TOKEN_CONTRACT_ADDRESS = "0xb738E4C0F9AFDd6BB50CcEA2267E6Af2413950bc";

  const connect = async () =>
    rLogin.connect().then(({ provider }) => {
      setDisConnects("connectsx");
      setDisablecls("disablenone");
      setProvider(provider);
      const accounts = provider
        .request({ method: "eth_accounts" })
        .then(([account]) => setAccount(account));
      account = accounts[0];
    });

  async function handleDisconnect() {
    try {
      await provider.disconnect({
        topic: sessions.topic,
        code: 6000,
        message: "User disconnected",
      });
      reset();
    } catch (e) {}
  }
  const reset = () => {
    setAccount([]);
  };

  const approve = async () => {
    window.web3 = await new Web3(provider);
    window.contract = await new window.web3.eth.Contract(
      ABI2,
      TOKEN_CONTRACT_ADDRESS
    );
    let balances = await Web3.utils.toWei(addamount, "ether");
    console.log("fufufufu", balances);
    let Tokenbal = addamount * 0.12;
    let Tokenbals = await Web3.utils.toWei(Tokenbal.toString(), "ether");
    console.log("ffffff", Tokenbals);
    await window.contract.methods
      .approve(CONTRACT_ADDRESS, Tokenbals)
      .send({ from: account })
      .then(async (re) => {
        console.log("aaaabbbbbb", re);
        console.log("aaaaaaaaaa", re.transactionHash);
        setaddTxHash(re.transactionHash);
        window.alert("Approve Success ");
      })
      .catch((err) => {
        console.log("errrrr", err);
        setaddTxHash("failed");
        window.alert("Approve Failed");
      });
  };

  const addliquidity = async () => {
    window.web3 = await new Web3(provider);
    window.contract = await new window.web3.eth.Contract(ABI, CONTRACT_ADDRESS);
    let balances = await Web3.utils.toWei(addamount, "ether");
    console.log("fufufufu", balances);
    let Tokenbal = addamount * 0.12;
    let Tokenbals = await Web3.utils.toWei(Tokenbal.toString(), "ether");
    console.log("ffffff", Tokenbals);
    await window.contract.methods
      .addliquidity(Tokenbals)
      .send({ from: account, value: balances.toString() })
      .then(async (re) => {
        console.log("aaaaaaaaaa", re.transactionHash);
        setaddTxHash(re.transactionHash);
        window.alert("Success ");
      })
      .catch((err) => {
        console.log("errrrr", err);
        setaddTxHash("Failed");
        window.alert("Failed");
      });
  };

  const removeliquidity = async () => {
    window.web3 = await new Web3(provider);
    window.contract = await new window.web3.eth.Contract(ABI, CONTRACT_ADDRESS);
    let balances = await Web3.utils.toWei(removeamount, "ether");
    console.log("fufufufu", balances);
    let Tokenbal = removeamount * 0.12;
    let Tokenbals = await Web3.utils.toWei(Tokenbal.toString(), "ether");
    console.log("ffffff", Tokenbals);
    await window.contract.methods
      .removeliquidity(balances, Tokenbals)
      .send({ from: account })
      .then(async (re) => {
        console.log("lp", re);
        console.log("lpr", re.transactionHash);
        setremoveTxHash(re.transactionHash);
        window.alert("Success ");
      })
      .catch((err) => {
        console.log("errrrr", err);
        setaddTxHash("Failed");
      });
  };



  const FufiToFusd = async () => {
    window.web3 = await new Web3(provider);
    window.contract = await new window.web3.eth.Contract(ABI, CONTRACT_ADDRESS);
    var amount = document.getElementById("amount").value;
    var balances = await Web3.utils.toWei(amount, "ether");
    await window.contract.methods
      .fufiToFusd()
      .send({ from: account, value: balances.toString() })
      .then(async (re) => {
        console.log("aaaaaaaaaa", re.transactionHash);
        window.alert("Swap Success ");
      })
      .catch((err) => {
        console.log("errrrr", err);
        window.alert("Swap Failed");
      });
  };

  const FusdToFufi = async () => {
    window.web3 = await new Web3(provider);
    window.contract = await new window.web3.eth.Contract(ABI, CONTRACT_ADDRESS);
    var amount = document.getElementById("amount").value;
    var balances = await Web3.utils.toWei(amount, "ether");
    await window.contract.methods
      .fusdtofufi(balances)
      .send({ from: account })
      .then(async (re) => {
        console.log("aaaaaaaaaa", re.transactionHash);
        window.alert("Swap Success ");
      })
      .catch((err) => {
        console.log("errrrr", err);
        window.alert("Swap Failed");
      });
  };

  const getFufilp = async () => {
    window.web3 = await new Web3(provider);
    window.contract = await new window.web3.eth.Contract(ABI, CONTRACT_ADDRESS);
     await window.contract.methods.getFufibalance(account).call().then(async (re) => {
      var datas = ethers.utils.formatEther(re)
    setFufilpbalance(datas);
    getFusdlp();
    getTotalFufilp();
    }).catch((err) => {
      console.log("errrrr", err);
      window.alert("Please cannect wallet ");
    });
  };

  const getFusdlp = async () => {
    window.web3 = await new Web3(provider);
    window.contract = await new window.web3.eth.Contract(ABI, CONTRACT_ADDRESS);
   await window.contract.methods.getFusdbalance(account).call().then(async (re) => {
    let datas =  ethers.utils.formatEther(re)
    setFusdtlpbalance(datas);
    }).catch((err) => {
      console.log("errrrr", err);
      window.alert("Please cannect wallet ");
    });
  };

  const getBalance = async () => {
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    await provider.getBalance(account).then(async (res) => {
   // console.log("Fufibalance", bal);
    let bal = Web3.utils.fromWei(res.toString(),'ether');
    let re = parseFloat(bal)
    setBalance(re);
    console.log("Fufibalance", re);
    getFusdbalance();
    getFufilp();
  })
  .catch((err) => {
    console.log("errrrr", err);
    window.alert("Please cannect wallet ");
  });
  };

  const getFusdbalance = async () => {
    window.web3 = await new Web3(provider);
    window.contract = await new window.web3.eth.Contract(ABI2, TOKEN_CONTRACT_ADDRESS);
    await window.contract.methods.balanceOf(account).call().then(async (re) => {
    let bal =  ethers.utils.formatEther(re)
    setFusdBalance(bal);
    console.log("Fusdbalance", bal);
  }).catch((err) => {
    console.log("errrrr", err);
    window.alert("Please cannect wallet ");
  });

  };

  const getTotalFufilp = async () => {
    window.web3 = await new Web3(provider);
    window.contract = await new window.web3.eth.Contract(ABI, CONTRACT_ADDRESS);
    await window.contract.methods.totalFufi().call().then(async (re) => {
    let datas =  ethers.utils.formatEther(re)
    setTotalFufilpbalance(datas);
    getTotalFusdlp();
  }).catch((err) => {
    console.log("errrrr", err);
    window.alert("Please cannect wallet ");
  });
  };

  const getTotalFusdlp = async () => {
    window.web3 = await new Web3(provider);
    window.contract = await new window.web3.eth.Contract(ABI, CONTRACT_ADDRESS);
    await window.contract.methods.totalFusd().call().then(async (re) => {
      var datas = ethers.utils.formatEther(re)
    setTotalFusdtlpbalance(datas);
    }).catch((err) => {
      console.log("errrrr", err);
      window.alert("Please cannect wallet ");
    });
  };

  const addhaindileAmount = (event) => {
    let to = parseFloat(event.target.value) * 0.12;
    setFusdamt(to);
    setaddamount(event.target.value);
  };

  const removehaindileAmount = (event) => {
    setValuex(false);
    let to = parseFloat(event.target.value) * 0.12;
    setRTotalam(to);
    setremoveamount(event.target.value);
  };

  const FufiswaphaindileAmount = (event) => {
    let to = parseFloat(event.target.value) * 0.12;
    setFufiswapFusdamt(to);
    setaddamount(event.target.value);
  };

  const FusdswaphaindileAmount = (event) => {
    let to = parseFloat(event.target.value) / 0.12;
    setFusdswapRTotalam(to);
    setaddamount(event.target.value);
  };
  const [valuex, setValuex] = useState(true);

  const stNewvalu = () => {
    setValuex(true);
  };

  return (
    <>
      <div
        class="container-fluid"
        style={{ paddingLeft: "0", paddingRight: "0" }}
      >
        <div className="topNavbars">
          <div class="Grid Grid--gutters Grid--cols-4 u-textCenter">
            <div class="Grid-cell">
              <div class="Demo">
                <div className="wlattes">
                  <span>wallet address:</span>
                  <span className="accountDFG">{account}</span>
                </div>
              </div>
            </div>
            <div class="Grid-cell">
              <div class="Demo">
                <RLoginButton id={disablecls} onClick={connect}>
                  Connect wallet Address
                </RLoginButton>
                <button className={disConnects} onClick={handleDisconnect}>
                  Disconnect
                </button>
              </div>
            </div>
            {/* <div class="Grid-cell"><div class="Demo">
   
      </div></div> */}
            
          </div>
        </div>
      </div>

      <div class="container">
        <div class="row">
          <div className="col-md-8 offset-md-2">
            <div className="classNamebx">
              <div className="tabPnales">
                <div class="tabset">
                  <input
                    type="radio"
                    name="tabset"
                    id="tab1"
                    aria-controls="marzen"
                    onClick={stNewvalu}
                    checked={valuex}
                  />
                  <label onClick={getBalance} for="tab1">
                    Add Liquidity
                  </label>
                  <input
                    type="radio"
                    name="tabset"
                    id="tab2"
                    aria-controls="rauchbier"
                  />
                  <label for="tab2">Remove Liquidity</label>
                  <div class="tab-panels">
                    <section id="marzen" class="tab-panel">
                      <div className="container">
                        <h4 style={{ textAlign: "center" }}>Add Liquidity</h4>
                        <div className="row">
                          <div className="col-md-8 offset-md-2">
                            <div className="btnaidity">
                              <div className="inputsAd">
                                <input
                                  type="text"
                                  className="inputs"
                                  value={addamount}
                                  onChange={addhaindileAmount}
                                  placeholder="FUFI"
                                  id="addamount"
                                />
                                <span className="Fufiox">Fufi</span>
                                <span className="inputAdx">
                                  Fufi balance :{balance}{" "}
                                </span>
                                {/* <p>{txHash}</p> */}
                              </div>

                              <div className="inputsAd">
                                <input
                                  type="text"
                                  className="inputs"
                                  value={Fusdamt}
                                  placeholder="FUSD"
                                />
                                <span className="Fufiox">FUSD</span>
                                <span className="inputAdx">
                                  Fusd balance :{Fusdbalance}{" "}
                                </span>
                              </div>
                            </div>
                          </div>
                        </div>
                        <div className="row">
                          <div className="col-md-8 offset-md-2">
                            <div className="btnaidity flxDFG">
                              <button
                                className="btnadliquidity"
                                onClick={approve}
                              >
                                approve
                              </button>
                              <button
                                className="btnadliquidity"
                                onClick={addliquidity}
                              >
                                Submit
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </section>
                    <section id="rauchbier" class="tab-panel">
                      <div className="container">
                        <h4 style={{ textAlign: "center" }}>
                          Remove Liquidity
                        </h4>
                        <div className="row">
                          <div className="col-md-8 offset-md-2">
                            <div className="btnaidity">
                              <div className="inputsAd">
                                <input
                                  type="text"
                                  className="inputs"
                                  value={removeamount}
                                  onChange={removehaindileAmount}
                                  placeholder="FUFI"
                                  id="removeamount"
                                />
                                <span className="Fufiox">Fufi</span>
                              </div>
                              {/* <p>{txHash1}</p> */}
                              <div className="inputsAd">
                                <input
                                  type="text"
                                  className="inputs"
                                  value={RFusdamt}
                                  placeholder="FUSD"
                                />
                                <span className="Fufiox">FUSD</span>
                              </div>
                            </div>
                          </div>
                        </div>
                        <div className="row">
                          <div className="col-md-8 offset-md-2">
                            <div className="btnaidity flxDFG">
                              <button
                                className="btnadliquidity"
                                onClick={removeliquidity}
                              >
                                Remove Liquidity
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </section>
                  </div>
                </div>
              </div>

              <div className="Proiquidity">
                <div class="row">
                  <div className="col-md-12">
                    <h6 style={{ textAlign: "center" }}>
                      Your Provided Liquidity
                    </h6>

                    <table className="tabkscf">
                      <thead>
                        <tr>
                          <th scope="col">Total FUFI LP</th>
                          <th scope="col">Total FUSD LP</th>
                          <th scope="col">Your FUFI LP</th>
                          <th scope="col">Your FUSD LP</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <td data-label="Total FUFI LP">{Totalfufilpbalance}{" "}</td>
                          <td data-label="Your FUSD LP">{Totalfusdtlpbalance}{" "} </td>
                          <td data-label="Your FUFI LP">{fufilpbalance} </td>
                          <td data-label="Total FUSD LP">{fusdtlpbalance} </td>

                        </tr>
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="container">
        <div class="row"></div>
      </div>
    </>
  );
};

export default Tradeox;
