import React, { useState } from 'react'
import { Link, useNavigate, NavLink } from "react-router-dom";
import TRADEOX from '../../assets/images/TRADEOX2.png'

import '../../assets/css/style.css'
import '../../assets/css/responsiveFlx.css'

const Navbar = () => {


 
  const [isActive, setActive] = useState("false");
  const sidbarClapse = () => {
    setActive(!isActive); 
   };

  return (
    <>

     



      {/* <nav id="sidebar" className={nactive}> */}
      <nav id="sidebar" className={isActive ? null : "active"}> 
        <div class="p-4 pt-5">
          <div class="contuid">
            <button type="button" id="sidebarCollapse" onClick={sidbarClapse} class="btn btn-primary">
              <i class="fa fa-bars"></i>
              <span class="sr-only">Toggle Menu</span>
            </button>
          </div>
          <a href="/" class="imglogo">
            <img src={TRADEOX} />
          </a>
          <ul class="list-unstyled components mb-5">
            {/* <li class="active">
	            <a href="#homeSubmenu" data-toggle="collapse" aria-expanded="false" class="dropdown-toggle">Home</a>
	            <ul class="collapse list-unstyled" id="homeSubmenu">
                <li>
                    <a href="#">Home 1</a>
                </li>
                <li>
                    <a href="#">Home 2</a>
                </li>
                <li>
                    <a href="#">Home 3</a>
                </li>
	            </ul>
	          </li> */}
            <li>
            <NavLink className="navLnksd" to="/">  
            Dashboard</NavLink>
            </li>
            <li>
            <NavLink className="navLnksd" to="/Swap">  
            Swap</NavLink>
            </li>
            <li>
              <a href="#pageSubmenu" data-toggle="collapse" aria-expanded="false" class="dropdown-toggle">Pages</a>
              <ul class="collapse list-unstyled" id="pageSubmenu">
                <li>
                  <a href="#">Page 1</a>
                </li>
                <li>
                  <a href="#">Page 2</a>
                </li>
                <li>
                  <a href="#">Page 3</a>
                </li>
              </ul>
            </li>
            <li>
              <a href="#">Portfolio</a>
            </li>
            <li>
              <a href="#">Contact</a>
            </li>
          </ul>
        </div>
      </nav>





    </>
  )
}

export default Navbar
